export class Board_members_view {

	constructor(){

	}

	open_and_close_menu_mobile = () =>{

		const menu_btn_mobile = 
		document.getElementById("admin_board_members_page_header_mobile_nav_btn");

		const menu_btn_close_mobile = document.getElementById("mobile-menu-close");

		const mobile_menu = document.getElementById("mobile-menu");

		menu_btn_mobile.addEventListener("click", (event)=>{

			mobile_menu.style.display = "none";
			mobile_menu.style.display = "flex";
		})

		menu_btn_close_mobile.addEventListener("click",(event)=>{

			mobile_menu.style.display = "none";
		})
	}

	render_board_documents = (state) => {

		const loading_bar = document.getElementById("admin_board_members_page_main_div");
		const no_documents_msg = document.getElementById("admin_board_members_page_main_h2");
		const yes_documents_msg = document.getElementById("admin_board_members_page_main_h21");
		const documents_section = document.getElementById("admin_board_members_page_main_section");

		// MOBILE 
		const loading_bar_mobile = document.getElementById("admin_board_members_page_main_div_mobile");
		const no_documents_msg_mobile= document.getElementById("admin_board_members_page_main_mobile_h2")
		const yes_documents_msg_mobile= document.getElementById("admin_board_members_page_main_mobile_h21");
		const documents_section_mobile = document.getElementById("admin_board_members_page_main_mobile_section");
		
		const loading = () => {

			loading_bar.style.display = 'none';
			no_documents_msg.style.display = 'none';
			yes_documents_msg.style.display = 'none';
			documents_section.style.display = 'none';

			loading_bar.style.display = 'block';

			//MOBILE
			loading_bar_mobile.style.display = 'none';
			no_documents_msg_mobile.style.display = 'none';
			yes_documents_msg_mobile.style.display = 'none';
			documents_section_mobile.style.display = 'none';

			loading_bar_mobile.style.display = 'block';	
		}

		const no_documents = () => {

			loading_bar.style.display = 'none';
			no_documents_msg.style.display = 'none';
			yes_documents_msg.style.display = 'none';
			documents_section.style.display = 'none';

			no_documents_msg.style.display = 'block';

			//MOBILE

			loading_bar_mobile.style.display = 'none';
			no_documents_msg_mobile.style.display = 'none';
			yes_documents_msg_mobile.style.display = 'none';
			documents_section_mobile.style.display = 'none';

			no_documents_msg_mobile.style.display = 'flex';
		}

		const render_documents = () => {

			loading_bar.style.display = 'none';
			no_documents_msg.style.display = 'none';
			yes_documents_msg.style.display = 'block';
			documents_section.style.display = 'flex';

			const board_documents_ul = 
			document.getElementById("admin_board_members_page_main_section_ul1")

			board_documents_ul.replaceChildren();

			state.board_documents.forEach((bd)=>{

				const li = document.createElement("li");
				const a = document.createElement("a");
				const span1 = document.createElement("span");
				const span2 = document.createElement("span");

				li.classList.add("admin_board_members_page_main_section_ul1_li");
				a.classList.add("admin_board_members_page_main_section_ul1_li_a");
				a.setAttribute("target", "_blank");
				if(process.env.NODE_ENV === "production"){
					a.setAttribute("href",bd.production_url)
				}
				else{
					a.setAttribute("href",bd.development_url)
				}
				span1.classList.add("admin_board_members_page_main_section_ul1_li_a_span1");
				span2.classList.add("admin_board_members_page_main_section_ul1_li_a_span2");

				span1.textContent = bd.name;
				span2.textContent = `Date Added: ${bd.date_added}`

				board_documents_ul.appendChild(li);
				li.appendChild(a);
				a.append(span1,span2);
			})

			// MOBILE

			loading_bar_mobile.style.display = 'none';
			no_documents_msg_mobile.style.display = 'none';
			yes_documents_msg_mobile.style.display = 'block';
			documents_section_mobile.style.display = 'flex';

			const board_documents_ul_mobile = 
			document.getElementById("admin_board_members_page_main_mobile_section_ul1");

			board_documents_ul_mobile.replaceChildren();

			state.board_documents.forEach((bd)=>{

				const li = document.createElement("li");
				const a = document.createElement("a");
				const span1 = document.createElement("span");
				const span2 = document.createElement("span");

				li.classList.add("admin_board_members_page_main_mobile_section_ul1_li");
				a.classList.add("admin_board_members_page_main_mobile_section_ul1_li_a");
				a.setAttribute("target", "_blank");
				if(process.env.NODE_ENV === "production"){
					a.setAttribute("href",bd.production_url)
				}
				else{
					a.setAttribute("href",bd.development_url)
				}
				span1.classList.add("admin_board_members_page_main_mobile_section_ul1_li_a_span1");
				span2.classList.add("admin_board_members_page_main_mobile_section_ul1_li_a_span2");

				span1.textContent = bd.name;
				span2.textContent = `Date Added: ${bd.date_added}`

				board_documents_ul_mobile.appendChild(li);
				li.appendChild(a);
				a.append(span1,span2);
			})
		}

		if(state.loading){

			loading();
			return
		}
		else if (state.loading === false &&
			state.board_documents.length === 0) {
			
			no_documents();
			return
		}
		else if (state.loading === false &&
			state.board_documents.length > 0) {
			
			render_documents();
			return
		}
	}

	nav_sort_by_event_listener = (modelFunction) => {

		const latest_btn = 
		document.getElementById("admin_board_members_page_main_section_nav_div_h52");
		const alphabetical_order_btn = 
		document.getElementById("admin_board_members_page_main_section_nav_div_h53");

		latest_btn.addEventListener("click", (event)=>{

			modelFunction("latest");
			alphabetical_order_btn.classList.remove("admin_board_members_page_main_section_nav_div_h5_selected");
			latest_btn.classList.remove("admin_board_members_page_main_section_nav_div_h5_selected");
			latest_btn.classList.add("admin_board_members_page_main_section_nav_div_h5_selected");
		})

		alphabetical_order_btn.addEventListener("click", (event)=>{

			modelFunction("alphabetical order");
			latest_btn.classList.remove("admin_board_members_page_main_section_nav_div_h5_selected");
			alphabetical_order_btn.classList.remove("admin_board_members_page_main_section_nav_div_h5_selected");
			alphabetical_order_btn.classList.add("admin_board_members_page_main_section_nav_div_h5_selected");
		})

		// MOBILE 

		const latest_btn_mobile = 
		document.getElementById("admin_board_members_page_main_mobile_section_nav_div_h52");
		const alphabetical_order_btn_mobile = 
		document.getElementById("admin_board_members_page_main_mobile_section_nav_div_h53");

		latest_btn_mobile.addEventListener("click", (event)=>{

			modelFunction("latest");
			alphabetical_order_btn_mobile.classList.remove("admin_board_members_page_main_mobile_section_nav_div_h5_selected");
			latest_btn_mobile.classList.remove("admin_board_members_page_main_mobile_section_nav_div_h5_selected");
			latest_btn_mobile.classList.add("admin_board_members_page_main_mobile_section_nav_div_h5_selected");
		})

		alphabetical_order_btn_mobile.addEventListener("click", (event)=>{

			modelFunction("alphabetical order");
			latest_btn_mobile.classList.remove("admin_board_members_page_main_mobile_section_nav_div_h5_selected");
			alphabetical_order_btn_mobile.classList.remove("admin_board_members_page_main_mobile_section_nav_div_h5_selected");
			alphabetical_order_btn_mobile.classList.add("admin_board_members_page_main_mobile_section_nav_div_h5_selected");
		})

	}
}