import {Board_members_view} from '../view/view.js';
import {Board_members_model} from '../model/model.js';

export class Board_members_controller {

	constructor(){

		this.view = new Board_members_view();
		this.model = new Board_members_model();

		this.view.open_and_close_menu_mobile();
		
		this.model.registerListener((state)=>{
			this.view.render_board_documents(state)
		});

		this.model.fetch_board_documents();

		this.view.nav_sort_by_event_listener(
			this.sort_board_documents
		)
		
	}

	sort_board_documents = (category) => {
		this.model.sort_board_documents(category)
	}

}