export class Board_members_model {

	constructor(){

		this.state = {
			loading: false,
			board_documents: []
		}

		this.listeners = [];
	}

	setState = (newState) => {
	  const deepMerge = (target, source) => {
	    for (const key in source) {
	      // If both sides are objects, merge recursively
	      if (
	        typeof target[key] === 'object' &&
	        typeof source[key] === 'object' &&
	        target[key] !== null &&
	        source[key] !== null &&
	        !Array.isArray(target[key]) &&
	        !Array.isArray(source[key])
	      ) {
	        deepMerge(target[key], source[key]);
	      } else {
	        // Otherwise just overwrite
	        target[key] = source[key];
	      }
	    }
	    return target;
	  };

	  this.state = deepMerge({ ...this.state }, newState);
	  this.notifyListeners();
	  console.log(this.state);
	  
	  return this.state;
	};

	registerListener = (listener) => {

    	this.listeners = [...this.listeners, listener];
    	listener(this.state);
    }

  
    notifyListeners = () => {
    	this.listeners.forEach((listener)=>{
    		listener(this.state)
    	})
    }

    fetch_board_documents = () => {

    	if(process.env.NODE_ENV === "production"){

    		this.setState({loading: true});

	    	fetch("https://www.abbeyroad.org.uk/api/admin_board_members_page/fetch_board_documents")
	    	.then((res)=>{
	    		return res.json()
	    	})
	    	.then((res)=>{

	    		if(res.success){

	    			this.setState({
	    				loading: false,
	    				board_documents : res.board_documents
	    			})
	    		}
	    		else{

	    			this.setState({
	    				loading: false
	    			})

	    			console.log("error getting board_documents from server")
	    		}
	    	})
	    	.catch((err)=>{

	    		this.setState({
	    				loading: false
	    			})

	    			console.log(err)
	    	})
    	}
    	else{

    		this.setState({loading: true});

	    	fetch("http://localhost:3001/api/admin_board_members_page/fetch_board_documents")
	    	.then((res)=>{
	    		return res.json()
	    	})
	    	.then((res)=>{

	    		if(res.success){

	    			this.setState({
	    				loading: false,
	    				board_documents : res.board_documents
	    			})
	    		}
	    		else{

	    			this.setState({
	    				loading: false
	    			})

	    			console.log("error getting board_documents from server")
	    		}
	    	})
	    	.catch((err)=>{

	    		this.setState({
	    				loading: false
	    			})

	    			console.log(err)
	    	})
    	}    	
    }

    sort_board_documents = (sort_by_category) => {

    	if(sort_by_category === "latest" &&
    		this.state.board_documents.length > 0){

    		this.setState({loading: true})

    		const latest_dates = [...this.state.board_documents].sort((a, b) => {
			    const [dayA, monthA, yearA] = a.date_added.split("/").map(Number);
			    const [dayB, monthB, yearB] = b.date_added.split("/").map(Number);

			    const dateA = new Date(yearA, monthA - 1, dayA);
			    const dateB = new Date(yearB, monthB - 1, dayB);

			    return dateB - dateA; // latest first
			  });


			this.setState({
				loading: false,
				board_documents: latest_dates
			})


    	}
    	else if (sort_by_category === "alphabetical order" &&
    		this.state.board_documents.length > 0) {
    		
    		this.setState({loading: true})

    		const ordered_names =[...this.state.board_documents].sort((a, b) => {
			    return a.name.localeCompare(b.name);
			});


    		this.setState({
				loading: false,
				board_documents: ordered_names
			})
    	}
    	else{
    		return
    	}
    }

}